import React from 'react';
import Title from './Title';
export default function Table({rows,cols,title,onRowClick,style,rowStyleMap}) {
    let rr;
    rr=rows
    if(!rr ||rr.length==0){
        return (<div>NO DATA</div>)
    }
    let cc =[]
    if(cols){
        cc=cols
    } else {
        Object.keys(rows[0]).forEach((key)=>{
            cc.push({field:key,display:key})
        })
    }
    let st={...{margin:5,overflowY:'auto'},...(style||{})}
    return (
        <div style={st}>
            <Title>{title}</Title>
                {(rows||[]).map((r,id)=>{
                    let rowStyle={margin:10,height:'60px'}
                    if(typeof rowStyleMap=="function"){
                        rowStyle={...rowStyle,...(rowStyleMap(r,id))}
                    }
                    return (<div  onClick={()=>{
                        if(onRowClick){
                            onRowClick(r,id)
                        }
                    }} style={rowStyle} className="mdl-card mdl-shadow--4dp">
                            <div className="mdl-card__title">
                                <h3 className="mdl-card__title-text">{r.Name}</h3>
                                <h4 className="mdl-card__subtitle-text">{r.AssetType}</h4>
                            </div>
                        <div className="mdl-card__supporting-text" >
                            <h4 className="mdl-card__subtitle-text">{r.BinName}</h4>
                            <h4 className="mdl-card__subtitle-text">{r.ParentName}</h4>
                            <h4 className="mdl-card__subtitle-text">{r.ParentBinName}</h4>
                        </div>

                    </div>)
                })}
        </div>
    )}